.dark-overlay {
    position: absolute;
    top:0;
    left:0;
    width: 100%;
    height: 100vh;
    background-color: #000;
    z-index: 10;
    opacity: 0.5;
}

.show-overlay {
    display: block;    
    overflow: hidden;

}

.hide-overlay {
    display: none;
}

.sidebar {
    position: absolute;
    top: 0;
    left: -270px;
    width: 270px;
    background-color: #fff;
    padding: 0 3em 0 1.5em ;
    margin: 0;
    z-index: 15;
    height: 100vh;
    border: 1px dotted #c7c7c7;
}

.sidebar ul {
    padding: 0.2em 0;
}

.show-sidebar {
    left: 0;
    -webkit-transition: 0.4s;
    transition: 0.4s; 
}

.hide-sidebar {
    left: -280px;
    -webkit-transition: 0.4s;
    transition: 0.4s; 
}

.sidebar-item {
    padding: 1em 0;
    text-decoration: none;
    list-style-type: none;
    cursor: pointer;
    background-color: #fff;
}

.sidebar-divider {
    width: 100%;
    color: #000;
}

.sidebar-header {
    padding: 0.8em 0;
    font-size: 0.8em;
    text-decoration: none;
    list-style-type: none;
    cursor: pointer;
    background-color: #fff;
}

.sidebar-subheader {
    margin: 1em 0 0.2 0;
    font-size: 1em;

}

.sidebar-profile-disc {
    overflow: hidden;
    width: 40px;
    height: 40px;
    border-radius: 20px;
    display: inline-block;
    margin-right: 1em;
    border: 1px solid black;
}

.sidebar-brand {
    font-size: 1.7em;
}

.sidebar-logo {
    margin-left: 0.8rem;
    margin-right: 0.5em;
}

.fa-file, .fa-address-book, .fa-bookmark, .fa-history {
    margin-right: 1.5em;
}


.login-warning-sidebar-message {
    color: #585858;
    font-size: 0.9em;
    margin-top: 3em;
}
