}


.avatar-username {
    justify-content: center;
    margin-top: 0.5em; 
    font-weight: bold;
    color: #1476d1;
}

.comment-area {
    width: 100% !important;
}

.comment-date {
    color: #707070;
    font-size: 0.9em;
}

.user-comment {
    color: #4377c6;
    font-weight: bold;
}

.user-comment::first-letter {
    text-transform: uppercase;
}

.replier-comment {
    color: #62a8fa;
    font-weight: bold;
    margin-right: 5px;
}

.replier-comment::first-letter {
    text-transform: uppercase;
}


.replied-comment {
    color: #62a8fa;
    margin-left: 10px;
}

.reply-arrow {
    color: #707070;
}

.comment-item {
    padding: 1.5em 2.5em;
    line-height: 1.8;
}

.reply-item {
    padding: 1.5em 2.5em;
    line-height: 1.8;
    color: #808080;

}


.comment-likes {
    cursor: pointer;
}


.comment-liked {
    color: #4377c6;
}

.comment-not-liked {
    color: #707070;

}

.like-mousedown {
    background-color: rgba(130, 130, 130, 0.3);
    color: #000;
    padding: 0.6em;
    border-radius: 50%;
    -webkit-transition: background-color 0.2s ease-out;
    transition: background-color 0.2s ease-out;
}

.like-mouseup {
    background-color: rgba(130, 130, 130, 0);
    padding: 0.5em;
    border-radius: 50%;
    -webkit-transition: background-color 1.5s;
    transition: background-color 1.5s;
}


